.bottom-bar-container {
  padding-top: 10px;
  padding-bottom: 10px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  background-color: rgba(80, 80, 80, 0.5);
  border-bottom: 1px solid #ffffff;
  position: absolute;
  width: 100%;
  bottom: 0;
}

.bottom-bar-container .menu-links {
  width: fit-content;
  text-decoration: none;
  color: #fff;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.bottom-bar-container .menu-texts {
  color: #fff;
  cursor: pointer;
}

.bottom-bar-container .menu-links svg {
  fill: #fff;
}

@media (max-width: 768px) {
  .bottom-bar-container {
    flex-direction: column;
  }
}
