.my-profile-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  max-height: 400px;
  min-height: 200px;
}

.my-profile-section .form-group {
  width: 500px;
  margin-bottom: 20px;
}

.my-profile-section .form-group .group-token-icons {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.my-profile-section .form-group .group-token-icons .token-item {
  border-radius: 100px;
  cursor: pointer;
  margin: 0 10px;
}

.my-profile-section .form-group .group-token-icons .token-item img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  border: 3px solid transparent;
}

.my-profile-section .form-group .group-token-icons .token-item img.active {
  border: 3px solid red;
}

.my-profile-section .form-group .input-for-back {
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  border: 1px solid #ffffff;
  border-radius: 10px;
  padding: 15px;
  box-shadow: rgba(0, 0, 0, 0.8) 0px 2px 4px,
    rgba(0, 0, 0, 0.6) 0px 7px 13px -3px,
    rgba(253, 253, 253, 0.6) 0px -3px 0px inset;
  position: relative;
}

.my-profile-section .form-group .input-for-back input::placeholder {
  color: #ddd;
}

.my-profile-section .form-group .input-for-back .btn-max-absolute {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  background-color: transparent;
  color: #ffffff;
  border-radius: 6px;
  border: 1px solid #ffffff;
  cursor: pointer;
  transition: 0.2s;
  padding: 7px 10px;
}

.my-profile-section .form-group .input-for-back .btn-max-absolute:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
