.top-bar-container {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 50px;
  padding-right: 50px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  background-color: rgba(80, 80, 80, 0.5);
  border-bottom: 1px solid #ffffff;
}

.logo-image {
  width: 200px;
}

.menu-link {
  width: fit-content;
}

.menu-link-item {
  color: #ffffff;
  font-size: 18px;
  cursor: pointer;
}

.menu-link-item:first-child {
  margin-right: 30px;
}

.menu-link-item:last-child {
  margin-left: 30px;
}

.top-bar-container > .menu-logo {
  flex: 1;
}

.top-bar-container > .menu-link {
  flex: 1;
}

.top-bar-container > .wallet-option {
  flex: 1;
}

@media (max-width: 768px) {
  .top-bar-container {
    flex-direction: column;
  }

  .top-bar-container > .menu-logo {
    margin: 10px 0;
  }

  .top-bar-container > .menu-link {
    margin: 10px 0;
  }

  .top-bar-container > .wallet-option {
    margin: 10px 0;
  }
}
